<template>
  <fieldset>
    <legend>Add an expense</legend>

    <label>
      Category:
      <input
        type="text"
        class="field"
        ref="category"
        v-model="expense.category"
        @keyup.enter="handleSubmit"
      />
    </label>

    <label>
      Amount
      <money
        class="field"
        ref="amount"
        @keyup.native.enter="handleSubmit"
        v-model="expense.amount"
        v-bind="money"
      ></money>
    </label>
  </fieldset>
</template>

<script>
  import { Money } from 'v-money';

  var Expense = function(amount, category) {
    this.amount = amount || 0;
    this.category = category || "";
  };

  export default {
    name: "ExpenseForm",

    data () {
      return {
        expense: new Expense(),

        money: {
          decimal: '.',
          thousands: ',',
          prefix: '$',
          suffix: '',
          precision: 2,
          masked: false,
        },
      };
    },

    props: {
      submitHandler: {
        required: false,
        type: Function,
        default: () => { return false; },
      },

      editingExpense: {
        required: false,
        type: Object,
        default: {},
      },
    },

    components: {
      Money,
    },

    watch: {
      editingExpense (exp) {
        this.expense = new Expense(exp.amount, exp.category);
      },
    },

    methods: {
      handleSubmit () {
        var valid = validateFields(this);

        if (valid) {
          this.submitHandler(this.expense);
          this.expense = new Expense();
          this.$refs.category.focus();
        } else {
          return false;
        }

        function validateFields(vm) {
          var amount = parseFloat(vm.expense.amount).toFixed(2),
              category = vm.expense.category;

          if (window.errorTimeout) {
            clearTimeout(window.errorTimeout);
            window.errorTimeout = null;
          }

          if ([0, NaN].includes(amount)) {
            vm.$refs.amount.$el.classList.add("field--error");

            window.errorTimeout = setTimeout(function() {
              vm.$refs.amount.$el.classList.remove("field--error");
            });

            vm.$refs.amount.$el.focus();
            vm.$refs.amount.$el.select();

            return false;
          } else if (!category.length) {
            vm.$refs.category.classList.add("field--error");

            window.errorTimeout = setTimeout(function() {
              vm.$refs.category.classList.remove("field--error");
            });

            vm.$refs.category.focus();

            return false;
          } else {
            return true;
          }
        }
      },
    },
  };
</script>

<style lang="scss">
  .field {
    transition: background-color 1.5s;
  }

  .field--error {
    background-color: Pink;
    transition: none;
  }
</style>



