<template>
  <span v-if="show == 'remaining'">
    <strong>Remaining:</strong>
    <Currency :value="amountRemaining" />
  </span>

  <span v-else-if="show == 'spent'">
    <strong>Total</strong>
    <Currency :value="amountSpent" />
  </span>

  <span v-else-if="show == 'perDay'">
    <strong>You can spend:</strong>
    <Currency :value="amountPerDay" />
    per day
  </span>
</template>

<script>
  import Storage from "./Storage";
  import Currency from './Currency';

  export default {
    name: "Calculations",

    props: {
      show: {
        required: true,
        type: String,
      },

      expenses: {
        required: false,
        type: Array,
        default: () => [],
      },

      daysRemaining: {
        required: false,
        type: Number,
      },

      amountPaid: {
        required: true,
        type: Number,
      },
    },

    components: {
      Currency,
    },

    computed: {
      amountSpent () {
        return this.expenses.reduce(function(acc, exp) {
          return acc + parseFloat(exp.amount);
        }, 0);
      },

      amountRemaining () {
        return parseFloat(this.amountPaid - this.amountSpent);
      },

      amountPerDay () {
        return (this.amountRemaining / this.daysRemaining);
      },
    },
  };
</script>



