<template>
  <div class="table" ref="expenseTable">
    <div class="table__head">
      <div class="table__row">
        <div class="
            table__cell
            table__cell--head
            table__cell--numeric
          "
        >
          Amount
        </div>

        <div class="table__cell table__cell--head">
          Category
        </div>

        <div class="table__cell table__cell--head">
          &nbsp;
        </div>
      </div>
    </div>

    <div class="table__body">
      <ExpenseRow
        v-for="(expense, i) in expenses"
        :editClickHandler="editClickHandler"
        :deleteClickHandler="deleteClickHandler"
        :key="i"
        :expense="expense"
        :i="i" />
    </div>

    <div class="table__foot">
      <div class="table__row">
        <div class="table__cell">
          <Calculations
            show="spent"
            :expenses="expenses"
            :amountPaid="amountPaid"
          />
        </div>

        <div class="table__cell">
          &nbsp;
        </div>

        <div class="table__cell">
          <Calculations
            show="remaining"
            :expenses="expenses"
            :amountPaid="amountPaid"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import ExpenseRow from './ExpenseRow';
  import Calculations from "./Calculations";

  export default {
    props: {
      expenses: {
        required: true,
        type: Array,
      },

      amountPaid: {
        required: true,
        type: Number,
      },

      editClickHandler: {
        required: false,
        type: Function,
        default: () => { return false; },
      },

      deleteClickHandler: {
        required: false,
        type: Function,
        default: () => { return false; },
      },
    },

    watch: {
      expenses (currentExps, previousExps) {
        if (currentExps.length > previousExps.length) {
          this.highlightInsertedRow();
        }
      },
    },

    components: {
      ExpenseRow,
      Calculations,
    },

    methods: {
      highlightInsertedRow () {
        var row = this.$refs
          .expenseTable
          .querySelector(".table__body .table__row:first-child");

        row.classList.add(
          "table__row--inserted",
          "table__row--highlighted"
        );

        setTimeout(function() {
          row.classList.remove("table__row--highlighted");

          setTimeout(function() {
            row.classList.remove("table__row--inserted");
          }, 1510);
        });
      },
    },
  }
</script>

<style lang="scss">
  .table {
    border: 1px solid #999;
  }

  .table__row {
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #999;
    transition: background-color 0.2s;
  }

  .table__row--inserted {
    transition: background-color 1.5s;
  }

  .table__row--highlighted {
    background-color: LightSteelBlue;
    transition: none;
  }

  .table__body .table__row:nth-child(even) {
    background: rgba(0, 0, 0, 0.025);
  }

  .table__body .table__row:hover {
    background: rgba(0, 0, 0, 0.09);
  }

  .table__head {
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.2);
  }

  .table__body {
    height: 250px;
    overflow-y: auto;
    overflow-x: hidden;
  }

  .table__foot {
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 0 -0.2rem 0.5rem rgba(0, 0, 0, 0.2);
  }

  .table__cell--head {
    font-weight: bold;
  }

  .table__cell {
    width: 50%;
    padding: 0.5rem 1rem;
  }

  .table__cell--numeric {
    text-align: right;
  }

  .table__cell--colspan-2 {
    width: 100%;
  }

  .table__foot {
    text-align: right;
  }
</style>



