var NonParser = function() {
  this.parse = function(v) { return v; };
};

var FloatParser = function() {
  this.parse = function(v) {
    return parseFloat(v);
  };
};

var JSONParser = function() {
  this.parse = function(v) {
    return JSON.parse(v);
  };
};

var NonFormatter = function() {
  this.format = function(v) { return v; };
};

var JSONFormatter = function() {
  this.format = function(v) {
    return JSON.stringify(v);
  };
};

var ParserFactory = function() {
  this.for = function(key) {
    switch(key) {
      case "FLOAT":
        return new FloatParser();

      case "JSON":
        return new JSONParser();

      default:
        return new NonParser();
    }
  };
};

var FormatterFactory = function() {
  this.for = function(key) {
    switch(key) {
      case "JSON":
        return new JSONFormatter();

      default:
        return new NonFormatter();
    }
  };
};

var Storage = {
  PREFIX: "DB",
};

export default {
  get (key, opts) {
    opts = opts || {};
    key = Storage.PREFIX + "." + key;

    var result = localStorage.getItem(key) || opts.default,
        parser = new ParserFactory().for(opts.parser);

    return parser.parse(result);
  },

  set (key, value, opts) {
    opts = opts || {};
    key = Storage.PREFIX + "." + key;

    var formatter = new FormatterFactory().for(opts.formatter);

    localStorage.setItem(key, formatter.format(value));
  },
};



