<template>
  <div id="db" class="grid">
    <div class="grid__col--3">
      <h1>{{ currentMonthName }}</h1>

      <ExpenseForm
        :submitHandler="recordExpense"
        :editingExpense="editingExpense"
      />

      <fieldset>
        <legend>Income settings</legend>

        <label>
          Income each payday
          <money v-model="amountPaid" v-bind="money"></money>
        </label>

        <label>
          First pay day
          <input
            type="date"
            v-model="formattedPayDay1"
          />
        </label>

        <label>
          Second pay day
          <input
            type="date"
            v-model="formattedPayDay2"
          />
        </label>
      </fieldset>
    </div>

    <div class="grid__col--9">
      <ExpenseTable
        :expenses="expenses"
        :amountPaid="amountPaid"
        :editClickHandler="editExpense"
        :deleteClickHandler="deleteExpense"
      />

      <p>
        <strong>Next pay day:</strong>
        {{ daysUntilNextPayDay }}
      </p>

      <p>
        <Calculations
          show="perDay" 
          :expenses="expenses"
          :daysRemaining="getDaysRemaining()"
          :amountPaid="amountPaid"
        />
      </p>
    </div>
  </div>
</template>

<script>
  import moment from 'moment';
  import { Money } from 'v-money';

  import Storage from './Storage';

  import ExpenseForm from './ExpenseForm';
  import ExpenseTable from './ExpenseTable';
  import Calculations from "./Calculations";

  export default {
    data () {
      return {
        amountPaid: Storage.get("amountPaid", {
          parser: "FLOAT",
          default: 0,
        }),

        money: {
          decimal: '.',
          thousands: ',',
          prefix: '$',
          suffix: '',
          precision: 2,
          masked: false,
        },

        currentMonthName: moment().format("MMMM"),

        editingExpense: null,
        editingRow: null,

        expenses: Storage.get("expenses", {
          parser: "JSON",
          default: "[]",
        }),

        payDay1: moment("2018-01-12"),
        payDay2: moment("2018-01-31"),
      };
    },

    components: {
      Money,
      ExpenseForm,
      ExpenseTable,
      Calculations,
    },

    watch: {
      expenses (currentExps, previousExps) {
        Storage.set(
          "expenses",
          this.expenses,
          { formatter: "JSON" }
        );
      },

      amountPaid (value) {
        Storage.set("amountPaid", value);
      },
    },

    computed: {
      formattedPayDay1: {
        get: function() {
          return this.payDay1.format("YYYY-MM-DD");
        },

        set: function(value) {
          this.payDay1 = moment(value);
        },
      },

      formattedPayDay2: {
        get: function() {
          return this.payDay2.format("YYYY-MM-DD");
        },

        set: function(value) {
          this.payDay2 = moment(value);
        },
      },

      daysUntilNextPayDay: function() {
        if (moment().startOf("day") < this.payDay1.startOf("day")) {
          return this.payDay1.startOf("day")
            .from(moment().startOf("day"));
        } else {
          return this.payDay2.startOf("day")
            .from(moment().startOf("day"));
        }
      },
    },

    methods: {
      recordExpense: function(expense) {
        var expenses = this.expenses.slice(0);

        if (this.editingRow != null) {
          expenses[this.editingRow] = expense;
        } else {
          expenses.unshift(expense);
        }

        this.editingRow = null;
        this.expenses = expenses;
      },

      deleteExpense: function(i) {
        this.expenses.splice(i, 1);
      },

      editExpense: function(i) {
        this.editingRow = i;
        this.editingExpense = this.expenses[this.editingRow];
      },

      getDaysRemaining: function() {
        if (moment() < this.payDay1) {
          return this.payDay1.startOf("day")
            .diff(moment().startOf("day"), "days");
        } else {
          return this.payDay2.startOf("day")
            .diff(moment().startOf("day"), "days");
        }
      },
    },
  }
</script>

<style lang="scss">
  #db {
    font-family: Helvetica Neue, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0 0 1rem;
    padding: 0;
  }

  fieldset {
    margin: 0 0 1rem;
    padding: 1rem;
  }

  label,
  input {
    display: block;
  }

  input {
    font-size: 1.25rem;
    padding: 0.5rem 1rem;
  }

  label {
    margin: 0 0 1rem;
  }

  [class*="grid__col--"] {
    margin-right: 1rem;
  }

  @media (min-width: 625px) {
    .grid {
      display: flex;
      width: 100%;
    }

    .grid__col--3 {
      width: 25%;
    }

    .grid__col--6 {
      width: 50%;
    }

    .grid__col--9 {
      width: 75%;
    }

    [class*="grid__col--"]:last-child,
    [class*="grid__col--"]:only-child {
      margin-right: 0;
    }
  }
</style>



